<template>
	<view class="popwnd-box" v-if="visible" @click="close">
		<view id="popwnd" class="popwnd">
			<view class="top">
				<view>{{strTitle}}</view>
			</view>
			<scroll-view class="bottom" scroll-y="true">
				<view v-for="(item, index) in lst" :key="index"
					:class="['bottom-item', {'bottom-active':curIdx==index}]" @tap="curIdx = index">
					<view v-if="item.hide == true"></view>
					<view v-else-if="strName.length > 0">{{item[strName]}}</view>
					<view v-else>{{item}}</view>
					<view v-if="item.child">{{item.child}}</view>
					<image v-if="curIdx==index" :src="ischeck" mode="aspectFit"></image>
					<image v-else :src="nocheck" mode="aspectFit"></image>
				</view>
			</scroll-view>
			<view class="foot">
				<view @tap="cancel" class="foot-cancel">取消</view>
				<view @tap="confirm" class="foot-ok">确认</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			visible: {
				type: Boolean,
				default: false,
				required: true,
			},
			strName: {
				type: String,
				default: '',
			},
			strTitle: {
				type: String,
				default: '',
				required: true,
			},
			sel: {
				type: Number | String,
				default:'0'
			},
			lst: {
				type: Array,
				default: () => []
			}
		},
		data() {
			return {
				curIdx: this.sel,
				nocheck: 'data:image/png;base64,',
				ischeck: 'data:image/png;base64,',
			}
		},
		watch: {
			sel(val) {
				this.curIdx = val;
			},
		},
		methods: {
			close(e) {
				//if(e.target.id != "popwnd")
				//	this.$emit('update:visible', false)
			},
			cancel() {
				this.curIdx = this.sel;
				this.$emit('update:visible', false)
			},
			confirm() {
				let name = '';
				if (this.strName.length > 0)
					name = this.lst[this.curIdx][this.strName];
				else
					name = this.lst[this.curIdx];

				let ret = {
					sel: this.curIdx,
					name: name
				};
				this.$emit('confirm', ret);
				this.cancel();
			},
			SelChange(idx) {
				this.curIdx = idx;
			},
		}
	}
</script>

<style lang="scss">
	.popwnd-box {
		z-index: 999;
		position: fixed;
		left: 0;
		top: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100vh;
		background: rgba(0, 0, 0, .2);
		transition: opacity .2s linear;
	}

	.popwnd {
		width: 600upx;
		background: white;
		border-radius: 20upx;
		overflow: hidden;

		display: flex;
		flex-direction: column;

	}

	.top {
		height: 108upx;

		view {
			//width: 170upx;
			//margin-top: 14upx;
			line-height: 108upx;
			font-size: 32upx;
			text-align: center;
			background-color: #85dbd0;
			color: #FFFFFF;
		}
	}

	.bottom {
		margin-top: 40upx;
		font-size: 30upx;
		line-height: 80upx;
		height: 50vh;

		&-item {
			width: 600upx;
			display: flex;
			align-items: center;

			view {
				margin-left: 50upx;
				flex: 1;
			}

			image {
				width: 30upx;
				height: 30upx;
				margin-right: 54upx;
			}
		}

		&-active {
			background-color: #f2faf9;
			color: #85dbd0;
		}
	}

	.foot {
		display: flex;
		height: 100upx;
		line-height: 100upx;

		&-cancel {
			width: 300upx;
			text-align: center;
			background-color: #f1f1f1;
		}

		&-ok {
			width: 300upx;
			text-align: center;
			background-color: #85dbd0;
			color: #ffffff;
		}
	}
</style>
